/* eslint-disable no-unused-expressions */
import { createOurReviewerTemplate } from '../templates/masakan-template';
import { generateId, dateId } from '../../utils/auto-generate';
import ICON from '../../component/icon';

const Reviewers = {
    // TODO: membuat tampilan utama Reviewers page
    async render() {
        return `
            <div style="display: flex; align-items: center; flex-direction: column">
                <h1 class="tf-uppercase detail-heading" style="margin: 0 !important; text-align: center;">berikan penilaian anda tentang kami</h1>
                <p style="padding: 0 20px; text-align: center;">Penilaian yang anda berikan akan memiliki pengaruh besar untuk kami</p>
                <div class="stars">
                    <i class="fa-solid fa-star"></i>
                    <i class="fa-solid fa-star"></i>
                    <i class="fa-solid fa-star"></i>
                    <i class="fa-solid fa-star"></i>
                    <i class="fa-solid fa-star"></i>
                </div>

                <form class="flx-column-start gap-10" id="myReviewer" method="POST">
                    <input type="text" class="field-content rounded-all" id="input-image" name="image"
                    aria-label="masukan url profil anda (opsional)" placeholder="link foto kamu (opsional)">
        
                    <input type="text" class="field-content rounded-all" id="input-name" name="nameUser"
                    aria-label="masukan nama lengkap" minlength="1" maxlength="18" placeholder="nama lengkap anda" required>
        
                    <div style="display: flex; column-gap: 6px; width: 100%;">
                        <input type="number" class="field-content rounded-all" id="input-age" name="age"
                        aria-label="masukan usia anda" placeholder="usia anda" min="10" max="150" onKeyUp="if(this.value>150){this.value='150';}else if(this.value<0){this.value='0';}" required>
                        <input type="text" class="field-content rounded-all" id="input-work" name="pekerjaan"
                        aria-label="masukan pekerjaan anda" placeholder="pekerjaan anda" onkeypress="return /^[0-9a-zA-Z \b]+$/i.test(event.key)" minlength="1" maxlength="20" required>
                    </div>
        
                    <textarea class="comment-pallete" id="input-comment" name="review" cols="30" rows="4" placeholder="masukan komentar anda" minlength="5" maxlength="150" onkeypress="return /^[0-9a-zA-Z \b]+$/i.test(event.key)" required></textarea>

                    <button class="btn-submit rounded-all">Kirim</button>
                </form>
            </div>
            <our-reviewers style="display: flex; justify-content: center; flex-direction: row; flex-wrap: wrap; gap: 1em; margin: 2.5em 1em;" id="our-review-users"></our-reviewers>
        `;
    },

    async afterRender() {
        const reviewerContainer = document.querySelector('our-reviewers');
        await fetch('./data/data-masakan-nusantara.json')
            .then((res) => res.json())
            .then((data) => {
                data.reviewerAplikasi.forEach((reviewer) => {
                    reviewerContainer.innerHTML += createOurReviewerTemplate(reviewer);
                });

                // TODO: menambahkan reviews
                const formReview = document.querySelector('#myReviewer');
                const reviewContainer = document.querySelector('#our-review-users');

                formReview.addEventListener('submit', async (event) => {
                    event.preventDefault();
                    const inputName = document.getElementById('input-name');
                    const inputImage = document.getElementById('input-image');
                    const inputAge = document.getElementById('input-age');
                    const inputWork = document.getElementById('input-work');
                    const inputComment = document.getElementById('input-comment');
                    const fotoUser = ICON._users;

                    const dataForm = {
                        reviewerId: generateId(),
                        namaReviewer: inputName.value,
                        imageUrl: inputImage.value ? inputImage.value : fotoUser,
                        umur: inputAge.value,
                        pekerjaan: inputWork.value,
                        rating: `${countRating() <= 0 ? '1' : countRating()}`,
                        tanggal: dateId(),
                        komentar: inputComment.value,
                    };

                    data.reviewerAplikasi.push(dataForm);
                    reviewContainer.innerHTML = `
                        ${data.reviewerAplikasi.map((read) => createOurReviewerTemplate(read)).join(' ')}
                    `;
                });
            });

        // TODO: membuat rating
        const stars = document.querySelectorAll('.stars i');
        stars.forEach((star, index1) => {
            star.addEventListener('click', () => {
                // eslint-disable-next-line no-shadow
                stars.forEach((star, index2) => {
                    // eslint-disable-next-line no-unused-expressions
                    index1 >= index2 ? star.classList.add('active') : star.classList.remove('active');
                });
                countRating();
            });
        });

        // TODO: Mengambil nilai rating
        const countRating = () => {
            const rateId = document.querySelectorAll('.stars .active');
            return rateId.length;
        };

        // TODO: menampilkan halaman selalu di atas
        window.scrollTo(0, 0);
    },
};

export default Reviewers;
