import 'lazysizes';
import 'lazysizes/plugins/parent-fit/ls.parent-fit';
import ICON from '../../component/icon';

// TODO: restaurant item
const createMasakanItemTemplate = (data) => `<div class="card restaurant-data">
    <div class="card-header">
        <picture>
            <source media="(max-width: 400px)" srcset="${data.imageUrl}">
            <img class="lazyload" data-src="${data.imageUrl}" alt="${data.namaMasakan}">
        </picture>   
    </div>
    <div class="body-card">
        <div class="title-card tf-capitalize fs-18" translate="no">
            <a href="/#/detail/${data.masakanId}">${data.namaMasakan}</a>
        </div>
        <div class="sub-title-card">
            <div class="rating">
                <img src="${ICON._star}" alt="rating"><span>${data.rating} (${data.reviewerMasakan.length} users)</span>
            </div>
            <div class="location">
                <img src="${ICON._place}" alt="location"><span>${data.provinsi}</span>
            </div>
            <div class="location">
                <img src="${ICON._clock}" alt="date"><span>${data.tanggal}</span>
            </div>
        </div>
        <div class="desc-card">
            ${data.deskripsi}
        </div>
    </div>
</div>`;

// TODO: detail restaurant
const createDetailMasakanTemplate = (data) => `
<div class="flx-column-center mt-3-em">
    <div class="detail-hero">
        <!-- image restaurant -->
        <div class="flx-column">
            <picture>
                <img class="restaurant-image lazyload" data-src="${data[0].imageUrl}" alt="${data[0].namaMasakan}">
            </picture>
        </div>

        <!-- about restaurant -->
        <div class="flx-column">
            <div class="flx-row-between mt-10" translate="no">
                <h2 class="tf-uppercase" tabindex=0>${data[0].namaMasakan}</h2>
                <div id="favoriteButtonContainer"></div>
            </div>
            <div class="flx-row">
                <div class="rating">
                    <img src="${ICON._star}" alt="rating"><span>${data[0].rating}&nbsp;(${data[0].reviewerMasakan.length} users)</span>
                </div>
                <div class="location">
                    <img src="${ICON._place}" alt="location"><span>${data[0].provinsi}</span>
                </div>
                <div class="location">
                    <img src="${ICON._clock}" alt="date"><span>${data[0].tanggal}</span>
                </div>
            </div>
            <p style="text-align:justify;" tabindex=0>${data[0].deskripsi}</p>
        </div>
    </div>

    <!-- cara memasak -->
    <h1 class="tf-uppercase detail-heading" style="margin: 0 !important; text-align: center;">video tutorial memasak</h1>
    <p style="padding: 0 20px; text-align: center;">Beberapa video rekomendasi dari kami bagaimana cara membuat <b class="tf-uppercase" style="letter-spacing: 3px;" translate="no">&nbsp;${data[0].namaMasakan}</b></p>
    <div class="detail-list">
        ${data[0].caraMemasak.map((cara) => `
        <div class="card">
            <div class="card-header">
            <iframe class="lazy" style="width: 100%; height: 300px;" src="${cara.urlVideo}" title="${cara.judul}" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>
            </div>
            <div class="body-card">
                <div class="title-card tf-capitalize">
                    <span tabindex=0>${cara.judul}</span>
                </div>
                <div class="flx-row" translate="no">
                    <div style="display:flex; align-items: center; font-size: medium;">
                        <img style="width: 21px; height:16px; margin-right: 8px;" src="${ICON._youtube}" alt="youtube channel"><span>${cara.namaChannel}</span>
                    </div>
                </div>
            </div>
        </div>`).join(' ')}
    </div>

    <h1 class="tf-uppercase detail-heading" style="margin: 0 !important; text-align: center;">lokasi penjual atau restoran</h1>
    <p style="padding: 0 20px; text-align: center;">Beberapa tempat rekomendasi untuk membeli <b class="tf-uppercase" style="letter-spacing: 3px;" translate="no">&nbsp;${data[0].namaMasakan}</b></p>
    <div class="detail-list">
        ${data[0].restoran.map((restaurant) => `
        <div class="card">
            <div class="card-header">
                <iframe src="https://www.google.com/maps/embed?pb=${restaurant.map}" style="
                border:0;
                width: 400px;
                height: 300px;
                max-width: 100%;
                max-height: 100%;" allowfullscreen="" loading="lazy" referrerpolicy="no-referrer-when-downgrade"></iframe>
            </div>

            <div class="body-card" translate="no">
                <div class="title-card tf-capitalize">
                    <span tabindex=0>${restaurant.namaRestoran}</span>
                </div>
                <div class="flx-row">
                    <div style="display:flex; align-items: flex-start; font-size: small;">
                        <img style="width: 21px; height:16px; margin-right: 8px;" src="${ICON._place}" alt="youtube channel"><span>${restaurant.alamat}</span>
                    </div>
                </div>
            </div>
        </div>`).join(' ')}
    </div>
    
    <!-- reviews section -->
    <h1 class="tf-uppercase detail-heading" style="margin: 0 !important; text-align: center; padding: 0 30px;">berikan komentar anda tentang ${data[0].namaMasakan}</h1>
    <p style="padding: 0 20px; text-align: center;">Komentar yang anda berikan memiliki pengaruh besar untuk kuliner di Indonesia</p>

    <div style="display: flex; align-items: center; flex-direction: column">
        <div class="stars">
            <i class="fa-solid fa-star"></i>
            <i class="fa-solid fa-star"></i>
            <i class="fa-solid fa-star"></i>
            <i class="fa-solid fa-star"></i>
            <i class="fa-solid fa-star"></i>
        </div>

        <form class="flx-column-start gap-10" id="myForm" method="POST">
            <input type="text" class="field-content rounded-all" id="input-image" name="image"
            aria-label="masukan url profil anda (opsional)" placeholder="link foto kamu (opsional)">

            <input type="text" class="field-content rounded-all" id="input-name" name="nameUser"
            aria-label="masukan nama lengkap" minlength="1" maxlength="18" placeholder="nama lengkap anda" required>

            <div style="display: flex; column-gap: 6px; width: 100%;">
                <input type="number" class="field-content rounded-all" id="input-age" name="age"
                aria-label="masukan usia anda" placeholder="usia anda" min="10" max="150" onKeyUp="if(this.value>150){this.value='150';}else if(this.value<0){this.value='0';}" required>
                <input type="text" class="field-content rounded-all" id="input-work" name="pekerjaan"
                aria-label="masukan pekerjaan anda" placeholder="pekerjaan anda" onkeypress="return /^[0-9a-zA-Z \b]+$/i.test(event.key)" minlength="1" maxlength="20" required>
            </div>

            <textarea class="comment-pallete" id="input-comment" name="review" cols="30" rows="4" placeholder="masukan komentar anda" minlength="5" maxlength="150" onkeypress="return /^[0-9a-zA-Z \b]+$/i.test(event.key)" required></textarea>
            <button class="btn-submit rounded-all">Kirim</button>
        </form>
    </div>

    <div class="detail-list" id="review-users">
        ${data[0].reviewerMasakan.map((read) => createOurReviewerTemplate(read)).join(' ')}
    </div>  
`;

// TODO: favorite button
const createFavoriteRestaurantButtonTemplate = () => `
    <button type="button" id="favoriteButton" aria-label="favorite this restaurant">
        <img class="icon-heart" src="${ICON._heartDisable}" alt="icon favorite">
    </button>
`;

// TODO: favorited button
const createUnfavoriteRestaurantButtonTemplate = () => `
    <button type="button" id="favoriteButton" style="padding: 10px; border: 1px solid #ffffff00;"
        aria-label="unfavorite this restaurant">
        <img class="icon-heart" src="${ICON._heartActive}" alt="icon favorite">
    </button>
`;

// TODO: reviewers
const createOurReviewerTemplate = (read) => `
    <div class="card-reviewer">
        <div class="" style="display: flex; justify-content: flex-start;">
            <img src=${read.imageUrl} 
                style="
                border-radius: 100%;
                border: 1px solid #fa63a0;
                background-size: cover;
                object-fit: cover;
                object-position: top;
                max-height: 60px;
                max-width: 60px;
                width: 100%;
                height: 100%;
                "
            alt="user-photo">
            <div class="" style="display: flex; flex-direction: column; margin-left: 1em; width: 100%;">
                <div class="" style="display: flex; justify-content: space-between; align-items: center;">
                    <div class="" style="text-transform: capitalize;" translate="no">${read.namaReviewer}</div>
                    <div class="" style="font-size: x-small;">${read.tanggal}</div>
                </div>
                
                <div class="" style="display: flex; justify-content: space-between; align-items: center;">
                    <div class="" style="display: flex; gap: 6px;">
                        ${ratingCount(read.rating)}
                    </div>
                    <div class="" style="font-size: x-small; text-transform: capitalize;" aria-label="usia pengguna">${read.umur} tahun</div>
                </div>
            </div>
        </div>

        <div class="" style="font-size: small; width: 100%; white-space: normal; margin: 1em 0;">
            ${read.komentar}
        </div>
        
        <div class="tf-capitalize" style="position: absolute; bottom: 5px; font-size: small; width: 274px; text-align: center;">
            - ${read.pekerjaan} -
        </div>
    </div>
`;

// TODO: rating count
const ratingCount = (data) => {
    let temp = '';
    for (let i = 0; i < data; i++) {
        temp += '<img src="./icon/star.svg" alt="star" width="15">';
    }

    return temp;
};

export {
    createMasakanItemTemplate,
    createDetailMasakanTemplate,
    createFavoriteRestaurantButtonTemplate,
    createUnfavoriteRestaurantButtonTemplate,
    createOurReviewerTemplate,
};
